import axios from 'axios'
import React, { Component, useState, useEffect } from 'react'

let api = axios.get('/CARS_API_1/db.json');


export class Cars extends Component {
    state = {
        cars : [],
        editeform : null,
        delForm : null,
        selectedId : -1
    }
    componentDidMount = () => {
        axios.get('/CARS_API_1/db.json')
        .then(res => {
            this.setState({ cars : res.data })
        })
    }
    getCars = () => {
        axios.get('/CARS_API_1/db.json')
        .then(res => {
            this.setState({ cars : res.data })
        })
    }


    handleDelete = async (car) => {
        document.getElementById(`Sure`).style.visibility = "hidden";
        console.log(`delete ${this.state.selectedId}`);
        axios.delete(`/https://jsonplaceholder.typicode.com/posts/${this.state.selectedId}`);
        this.getCars();
        
        
    }
    sureDel = (car, name) => {
        this.setState({selectedId : car})
        let delForm_in = <div>
            <p className="sure-p">
                    Are you sure you wanna delete <span>{name}</span> from the database
                </p>
                <button onClick={this.handleDelete} className="sure-del">delete</button>
        </div>
        this.setState({delForm : delForm_in})
        document.getElementById(`Sure`).style.visibility = "visible";
    }


    handleEdite = (val) => {
        console.log(`edite ${this.state.selectedId}`)
        document.getElementById(`Edite`).style.visibility = "hidden";
        axios.patch(`https://jsonplaceholder.typicode.com/posts/${this.state.selectedId}`, { title : val })
        .then(res => console.log(`res`, res, val))
        console.log(`val`, val)
    }
    edite = (car, name) => {
        this.setState({selectedId : car})
        let editeform_in = <div className="inputs">
            <div className="in-left">
                <p>{car}</p>
                <p>{name}</p>
                <p>new price :</p>
            </div>
            <div className="in-right">
                <input id="new-price" type="text" />
            </div>
        </div>
        this.setState({editeform : editeform_in})
        document.getElementById(`Edite`).style.visibility = "visible";
    }


    handleAdd = (id, name, price) => {
        console.log(`id`, id)
        console.log(`name`, name)
        console.log(`price`, price)

        document.getElementById(`add-box`).style.visibility = "hidden";

        axios.post('https://jsonplaceholder.typicode.com/posts',{
            id : id,
            name : name,
            price : price
        })
        .then(res => console.log(`res`, res))
        .catch(err => console.log(`err`, err))
    }
    add = () => {
        document.getElementById(`add-box`).style.visibility = "visible";
    }

    render() {
        return (
            <div className="box">
            <div className="head-box">
                <h3>id</h3>
                <h3>name</h3>
                <h3>price</h3>
            </div>


            {
                this.state.cars.map(car => <div className="item" key={car.id}>
                    <div className="edite" onClick={()=>this.edite(car.id, car.name)}>edite</div>
                    <p>{car.id}</p>
                    <p>{car.name}</p>
                    <p>{car.price} $</p>
                    <div className="delete" onClick={()=>this.sureDel(car.id, car.name)}>*</div>
                </div>)
            }


            <div onClick={this.add} className="add">
                +
                <div className="add-body"></div>
            </div>
            <div id="add-box" className="add-box over-box">
                <div className="inputs">
                    <div className="in-left">
                        <p>id :</p>
                        <p>name :</p>
                        <p>price :</p>
                    </div>
                    <div className="in-right">
                        <input id="id" type="text" />
                        <input id="name" type="text" />
                        <input id="price" type="text" />
                    </div>
                </div>
                <button onClick={ ()=>{
                    this.handleAdd(
                        document.getElementById("id").value,document.getElementById("name").value,document.getElementById("price").value,
                    )
                    } }>Add</button>
            </div>
            <div className="sure over-box" id="Sure">
                {this.state.delForm}
            </div>
            <div className="edite-box over-box" id="Edite">
                {this.state.editeform}
                <button onClick={()=>{this.handleEdite(document.getElementById("new-price").value)}}>edite</button>
            </div>
            </div>
        )
    }
}


{/* <Dele id={car.id} name={car.name} price={car.price} /> */}
function Dele (props) {
    const clas = `Del${props.id}`
    useEffect(() => {
        console.log(`null`, `Del${props.id}`);
        console.log(`object`, document.getElementById(`Del${props.id}`))

    }, [])
    return(
        <div className="Dele" id={clas} >
            <h1>delete {props.id}</h1>
            <button className="">
                delete
            </button>
        </div>
    )
}


export default Cars

